import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import { HomeListReducer } from './Reducers/HomeReducer';
import { userLoginReducer, passengerRegisterReducer, userDetailReducer, userUpdateProfileReducer, driverRegisterReducer } from './Reducers/UserReducers';

const reducer = combineReducers({
    homeList:  HomeListReducer,
    userLogin: userLoginReducer,
    passengerRegister: passengerRegisterReducer,
    userDetails: userDetailReducer,
    userUpdateProfile: userUpdateProfileReducer,
    driverRegister: driverRegisterReducer,


});

// get storage
const userInfoFromStorage = localStorage.getItem('userInfo') !== undefined && localStorage.getItem('userInfo')  ?
JSON.parse(localStorage.getItem('userInfo')): null;


const initialState = {
    userLogin: {
        userInfo: userInfoFromStorage
    }
}

const middleware = [thunk]

const store = createStore(reducer, initialState, composeWithDevTools(applyMiddleware(...middleware)));

export default store;